body {
  margin: 0;
  padding: 0;
  font-family: "Trebuchet MS", "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans", sans-serif;
  background: -webkit-linear-gradient(1deg, #FF5EDF 0%, #04C8DE 100%);
  background: linear-gradient(89deg, #FF5EDF 0%, #04C8DE 100%);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.center {
  display: flex;
  justify-content: center;
}

.particles {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: -1;
}
.Tilt {
    /*background: linear-gradient(89deg, #FF5EDF 0%, #04C8DE 100%);*/
    /*margin-top: -7%;*/
    float: left;
}
.form {
    width: 700px;
    background-color: #556;
    background-image: -webkit-linear-gradient(60deg, #445 12%, transparent 12.5%, transparent 87%, #445 87.5%, #445),
    -webkit-linear-gradient(300deg, #445 12%, transparent 12.5%, transparent 87%, #445 87.5%, #445),
    -webkit-linear-gradient(60deg, #445 12%, transparent 12.5%, transparent 87%, #445 87.5%, #445),
    -webkit-linear-gradient(300deg, #445 12%, transparent 12.5%, transparent 87%, #445 87.5%, #445),
    -webkit-linear-gradient(30deg, #99a 25%, transparent 25.5%, transparent 75%, #99a 75%, #99a),
    -webkit-linear-gradient(30deg, #99a 25%, transparent 25.5%, transparent 75%, #99a 75%, #99a);
    background-image: linear-gradient(30deg, #445 12%, transparent 12.5%, transparent 87%, #445 87.5%, #445),
    linear-gradient(150deg, #445 12%, transparent 12.5%, transparent 87%, #445 87.5%, #445),
    linear-gradient(30deg, #445 12%, transparent 12.5%, transparent 87%, #445 87.5%, #445),
    linear-gradient(150deg, #445 12%, transparent 12.5%, transparent 87%, #445 87.5%, #445),
    linear-gradient(60deg, #99a 25%, transparent 25.5%, transparent 75%, #99a 75%, #99a),
    linear-gradient(60deg, #99a 25%, transparent 25.5%, transparent 75%, #99a 75%, #99a);
    background-size:80px 140px;
    background-position: 0 0, 0 0, 40px 70px, 40px 70px, 0 0, 40px 70px;
}
.bounding-box {
    position: absolute;
    box-shadow: 0 0 0 3px #149df2 inset;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    cursor: pointer;
}

#inputimage {
    margin: 1%;
}
.container {
    background-color: darkgrey;
    padding: 1%;
    margin: 1%;
    border-radius: 2%;
}

.predictedPerson {
    background-color: #04C8DE;
    margin: 1%;
    padding: 1%;
    border-radius: 2%;
}

.title {
    padding: 1%;
    text-align: left;
}

.section {
    text-align: left;
    padding-left: 1%;
}

.attribute {
    float: left;
    padding-left: 1%;
}

.element:hover {
    background-color: mediumpurple;
    cursor: pointer;
}

.element {
    display: flex;
    justify-content: space-between;
}
